.header {
  text-align: center;
  margin-bottom: 2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.header__title {
  font-weight: 500;
}

.header__subtitle {
  color: #757575;
}

.header__subtitle__blinking {
  -webkit-animation: blink 1s ease-in-out infinite alternate;
          animation: blink 1s ease-in-out infinite alternate;
}

@-webkit-keyframes blink {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes blink {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.header__button {
  background: none;
  border: none;
  font-family: 'Lato', sans-serif;
  font-size: 0.85rem;
  font-weight: 500;
  text-transform: uppercase;
  margin-top: 0.75rem;
  padding: 0 0.5rem;
  cursor: pointer;
  -webkit-transition: height 0.2s cubic-bezier(0, 0, 0.2, 1);
  -o-transition: height 0.2s cubic-bezier(0, 0, 0.2, 1);
  transition: height 0.2s cubic-bezier(0, 0, 0.2, 1);
  overflow: hidden;
}

.header__button--visible {
  height: 2rem;
  opacity: 0;
  -webkit-animation: fade-in 0.5s cubic-bezier(0, 0, 0.2, 1) 0.1s forwards;
          animation: fade-in 0.5s cubic-bezier(0, 0, 0.2, 1) 0.1s forwards;
}

@-webkit-keyframes fade-in {
  to {
    opacity: 1;
  }
}

@keyframes fade-in {
  to {
    opacity: 1;
  }
}

.header__button--hidden {
  height: 0;
  opacity: 1;
  -webkit-animation: fade-out 0.2s cubic-bezier(0, 0, 0.2, 1);
          animation: fade-out 0.2s cubic-bezier(0, 0, 0.2, 1);
}

@-webkit-keyframes fade-out {
  to {
    opacity: 0;
  }
}

@keyframes fade-out {
  to {
    opacity: 0;
  }
}


