.period {
  width: 40%;
  max-width: 8.5rem;
  margin: 1.2rem 0.75rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.period__chart {
  width: 100%;
  margin-bottom: 0.75rem;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.period__chart * {
  cursor: pointer;
}

.period__chart h2 {
  -webkit-transform-origin: center -250%;
      -ms-transform-origin: center -250%;
          transform-origin: center -250%;
  -webkit-transition: -webkit-transform 0.2s cubic-bezier(0, 0, 0.2, 1);
  transition: -webkit-transform 0.2s cubic-bezier(0, 0, 0.2, 1);
  -o-transition: transform 0.2s cubic-bezier(0, 0, 0.2, 1);
  transition: transform 0.2s cubic-bezier(0, 0, 0.2, 1);
  transition: transform 0.2s cubic-bezier(0, 0, 0.2, 1), -webkit-transform 0.2s cubic-bezier(0, 0, 0.2, 1);
}

.period__chart:active h2 {
  -webkit-transform: scale(0.85);
      -ms-transform: scale(0.85);
          transform: scale(0.85);
}

.period__text {
  text-align: center;
  font-weight: 500;
}

@media screen and (min-width: 21rem) {
  .period {
    margin: 1.2rem;
  }
}

@media screen and (min-width: 37rem) {
  .period {
    margin: 1.2rem 0.75rem;
  }
}

@media screen and (min-width: 68rem) {
  .period {
    margin: 1.2rem;
    max-width: 10rem;
  }
}


