@import 'https://fonts.googleapis.com/css?family=Lato:400,500';

html,
*,
*:before,
*:after {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-weight: 400;
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
  outline: none !important;
}

html,
body {
  height: 100%;
}

html {
  font-size: 16px;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #ffffff;
}

#root {
  font-family: 'Lato', sans-serif;
  line-height: 1.4;
  color: #212121;
  margin: auto;
}

#root > * {
  max-width: 50rem;
  margin: 1rem 0;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 1.3rem;
}

h3 {
  font-size: 1.2rem;
  font-weight: 400;
}

@media screen and (min-width: 21rem) {
  #root > * {
    margin: 2rem 0;
  }
}

@media screen and (min-width: 48rem) {
  #root > * {
    margin: 3rem 0;
  }

  html {
    font-size: 18px;
  }
}

@media screen and (min-width: 68rem) {
  html {
    font-size: 20px;
  }
}


