.donut {
  position: relative;
}

.donut__text {
  position: absolute;
  text-align: center;
  top: 49%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 100%;
}

.donut__graph {
  width: 100%;
  height: 100%;
  -webkit-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

.donut__graph__background {
  stroke: #E0E0E0;
}

.donut__graph__fill {
  -webkit-transition: stroke-dashoffset 1s cubic-bezier(0, 0, 0.2, 1);
  -o-transition: stroke-dashoffset 1s cubic-bezier(0, 0, 0.2, 1);
  transition: stroke-dashoffset 1s cubic-bezier(0, 0, 0.2, 1);
}


